.App {
  text-align:left;
  margin: auto;
  width: 90vmin;
  border-style :solid; border-width: 2px ; 

}

.App-logo {
  height: 40vmin;
  width: 90vmin;
  pointer-events: none;
}

.myinput{  
  width: 80vmin;  
  height: 3vmin;
  text-align: left;
  margin-left: 5vmin;
}

.mybutton{
  width: 20vmin;  
  height: 3vmin;
  text-align: center;
  margin-top: 1vmin;
  margin-left: 5vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: top;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

